@import url(https://fonts.googleapis.com/css2?family=Saira:wght@500&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --button-color: #ff8906;
  --button-text-color: #fffffe;
  --background-color: #0f0e17;
  --text-color: #fffffe;
  overflow-x: hidden;

  background-color: var(--background-color);
  margin: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
* {
  box-sizing: border-box;
  font-family: Saira, sans-serif;
}

.Header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;

  -webkit-justify-content: space-between;

          justify-content: space-between;
  padding: 60px;
}
.web {
  color: var(--button-color);
}
.profile {
  color: var(--text-color);
  text-align: right;
  -webkit-flex: 50% 1;
          flex: 50% 1;
  font-family: serif;
  padding-top: 10px;
  margin: 20px;
  font-size: 20px;

  margin-right: auto;
}
img {
  border-radius: 50%;
}
.hello {
  font-size: 40px;
}

@media screen and (min-width: 0px) and (max-width: 400px) {
  .Header {
    display: block;
  }
  .profile {
    width: 90%;
  }
}

.contact {
  color: var(--text-color);
  background: transparent;
}
.Footer {
  background: transparent;
  padding: 25px 50px;
}
a.social {
  margin: 0 0.5rem;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  display: inline-block;
  color: var(--text-color);
}
a.social:hover {
  color: var(--button-color);
}

.links {
  padding: 20px;
}
@media screen and (min-width: 0px) and (max-width: 400px) {
  .Footer {
    display: block;
  }
}

.Tech {
  text-align: center;
  color: var(--text-color);

  font-size: 50px;
}
svg {
  height: 60px;
  width: 60px;
  margin: 20px;
}
.Technologies {
  width: 50%;
  margin: 10px auto;
}
.express {
  height: 60px;
  width: 60px;
  margin: 20px;
  -webkit-filter: invert(50%);
          filter: invert(50%);
}

@media screen and (min-width: 0px) and (max-width: 400px) {
  .Technologies {
    display: block;
    width: 80%;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;

  text-rendering: optimizeLegibility;
}

.input {
  width: 100%;
  padding: 20px;
  border: none;
  border-bottom: 1px solid var(--button-color);
  height: 100%;
  border-radius: 4px;
  background-color: transparent;
  resize: vertical;
  outline: none;
  color: var(--background-color);
}
.Contact {
  width: 100%;
  padding: 40px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.container {
  max-width: 100%;
  width: 40%;
  margin: auto;
  position: relative;
  background: var(--text-color);
  border-radius: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12),
    0 4px 4px rgba(0, 0, 0, 0.12), 0 8px 8px rgba(0, 0, 0, 0.12), 0 16px 16px rgba(0, 0, 0, 0.12);

  padding: 30px;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}
.button {
  position: relative;
  padding: 10px 10px;
  border: none;
  background: none;
  cursor: pointer;

  font-family: 'serif';
  font-weight: 30;
  text-transform: uppercase;
  font-size: 20px;
  color: var(--button-text-color);
  margin-top: 20px;
  background-color: var(--button-color);
  box-shadow: var(--shadow-color) 2px 2px 22px;
  border-radius: 4spx;
  z-index: 0;
  overflow: hidden;
}
.button:focus {
  outline-color: transparent;
  box-shadow: var(--button-color) 2px 2px 22px;
}
.right::after,
.button::after {
  display: block;
  position: absolute;
  white-space: nowrap;
  padding: 40px 40px;
  pointer-events: none;
}

.button::after {
  font-weight: 200;
  top: -30px;
  left: -20px;
}

.right,
.left {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}
.right {
  left: 66%;
}
.left {
  right: 66%;
}
.right::after {
  top: -30px;
  left: calc(-66% - 20px);

  background-color: var(--background-color);
  color: var(--button-color);
  transition: -webkit-transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out;
  -webkit-transform: translate(0, -90%) rotate(0deg);
          transform: translate(0, -90%) rotate(0deg);
}

.button:hover .right::after {
  -webkit-transform: translate(0, -47%) rotate(0deg);
          transform: translate(0, -47%) rotate(0deg);
}

.button .right:hover::after {
  -webkit-transform: translate(0, -50%) rotate(-7deg);
          transform: translate(0, -50%) rotate(-7deg);
}

.button .left:hover ~ .right::after {
  -webkit-transform: translate(0, -50%) rotate(7deg);
          transform: translate(0, -50%) rotate(7deg);
}

/* bubbles */
.button::before {
  content: '';
  pointer-events: none;
  opacity: 0.6;
  background: radial-gradient(
      circle at 20% 35%,
      transparent 0,
      transparent 2px,
      var(--text-color) 3px,
      var(--text-color) 4px,
      transparent 4px
    ),
    radial-gradient(
      circle at 75% 44%,
      transparent 0,
      transparent 2px,
      var(--text-color) 3px,
      var(--text-color) 4px,
      transparent 4px
    ),
    radial-gradient(
      circle at 46% 52%,
      transparent 0,
      transparent 4px,
      var(--text-color) 5px,
      var(--text-color) 6px,
      transparent 6px
    );

  width: 100%;
  height: 300%;
  top: 0;
  left: 0;
  position: absolute;
  -webkit-animation: bubbles 5s linear infinite both;
          animation: bubbles 5s linear infinite both;
}

@-webkit-keyframes bubbles {
  from {
    -webkit-transform: translate();
            transform: translate();
  }
  to {
    -webkit-transform: translate(0, -66.666%);
            transform: translate(0, -66.666%);
  }
}

@keyframes bubbles {
  from {
    -webkit-transform: translate();
            transform: translate();
  }
  to {
    -webkit-transform: translate(0, -66.666%);
            transform: translate(0, -66.666%);
  }
}

@media screen and (min-width: 0px) and (max-width: 600px) {
  .button {
    padding: 5px 20px 5px;
  }
  .container {
    width: 80%;
    padding: 20px;
    margin: auto;
  }
}

.ball {
  position: absolute;
  border-radius: 100%;
  opacity: 0.7;
  z-index: -1;
}

.themes {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
label {
  pointer-events: none;
}
label .input {
  display: none;
}
label .input:checked + .toggle-wrapper {
  box-shadow: 0 8px 14px 0 rgba(18, 51, 215, 0.12);
}
label .input:checked + .toggle-wrapper > .selector {
  left: 40px;
  background-color: #100c17;
}

label .toggle-wrapper {
  position: relative;
  width: 60px;
  height: 30px;
  background-color: var(--button-color);
  border-radius: 999px;
  margin: 10px;
  cursor: pointer;
  pointer-events: all;
  box-shadow: 0 8px 14px 0 rgba(215, 60, 18, 0.12);
}
label .toggle-wrapper .selector {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  left: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: #fdf8e3;
  transition: left 0.25s ease;
  border-radius: 50%;
}

